import React from "react";
import { CodeEditor, Language } from "@patternfly/react-code-editor";

const CodeEditorSizeToFit: React.FunctionComponent = () => {
  const onEditorDidMount = (editor: any, monaco: any) => {
    editor.layout();
    editor.focus();
    monaco.editor.getModels()[0].updateOptions({ tabSize: 5 });
  };

  const code = `<!-- DTD -->
  <!DOCTYPE note [
    <!ELEMENT note (to, from, heading, body)>
    <!ELEMENT to (#PCDATA)>
    <!ELEMENT from (#PCDATA)>
    <!ELEMENT heading (#PCDATA)>
    <!ELEMENT body (#PCDATA)>
  ]>
  <note>
    <to></to>
    <from></from>
    <heading></heading>
    <body>     3  .</body>
  </note>
  
  <!-- XML Schema -->
  <xs:schema xmlns:xs="http://www.w3.org/2001/XMLSchema">
    <xs:element name="note">
      <xs:complexType>
        <xs:sequence>
          <xs:element name="to" type="xs:string"/>
          <xs:element name="from" type="xs:string"/>
          <xs:element name="heading" type="xs:string"/>
          <xs:element name="body" type="xs:string"/>
        </xs:sequence>
      </xs:complexType>
    </xs:element>
  </xs:schema>
  
  <note>
    <to></to>
    <from></from>
    <heading></heading>
    <body>     3  .</body>
  </note>
  `;

  return (
    <CodeEditor
      isLanguageLabelVisible={true}
      isReadOnly={true}
      code={code}
      language={Language.xml}
      onEditorDidMount={onEditorDidMount}
      height="sizeToFit"
    />
  );
};

const text =
  "DTD (Document Type Definition)   XML-      . , DTD  ,   <note>    <to>, <from>, <heading>  <body>,        (PCDATA). XML Schema,   ,    ,     . , XML Schema  ,   <note>     <to>, <from>, <heading>  <body>,      .";

export const FourthPage = (
  <div>
    <h1> XML-</h1>
    <section>
      <br />

      <div>
        <p>
           XML-      
              . XML- 
          ,     ,  
                (DTD)  XML Schema.
            ,     
            .
        </p>
        <br />
        <p>
               XML-:  
          DTD (Document Type Definition)  XML Schema. DTD  
             XML-,    
          . XML Schema      ,
              ,    
          .
        </p>
        <br />
        <p>{text}</p>
      </div>

      <br />
      <b> :</b>
      <br />
      <CodeEditorSizeToFit></CodeEditorSizeToFit>
    </section>
  </div>
);
